























import Swal from 'sweetalert2';
import { Component, Ref, Vue } from 'vue-property-decorator';
import { Action, State, Getter } from 'vuex-class';
import { IInputInfo, ILoginPayload } from '@/types/interface';
import * as commonTypes from '@/types/common';
import { changeMyPw } from '@/utility';
import banner from '../../components/Banner.vue';
import inputFormPanel from '../../components/InputFormPanel.vue';
import BaseFooter from '../../components/BaseFooter.vue';

@Component({
  components: {
    banner,
    inputFormPanel,
    BaseFooter,
  },
})

export default class EditMemberProfile extends Vue {
  @Ref('inputForm') readonly inputForm!: inputFormPanel;
  @Ref('passwordForm')readonly passwordForm!: inputFormPanel;
  @State('userId') stateUserId !: string;
  @Getter('isMobileLogin') isMobileLogin!: boolean;
  @Action('addMember') addMember!: (params: IInputInfo) => Promise<ILoginPayload>;
  @Action('saveBaseUser') saveBaseUser!: (params: IInputInfo) => Promise<ILoginPayload>;
  @Action('loadBaseUser') loadBaseUser!: () => Promise<ILoginPayload>;

  inputList = [commonTypes.INPUT_ID, commonTypes.INPUT_NAME,
    commonTypes.INPUT_PHONE, commonTypes.INPUT_EMAIL, commonTypes.INPUT_FULL_ADDRESS];

  passwordList = [commonTypes.INPUT_PASSWORD, commonTypes.INPUT_NEW_PASSWORD];

  // created() {
  //   this.loadBaseUser();
  // }

  link() {
    this.$router.push('/member');
  }

  save() {
    const info: IInputInfo | undefined = this.inputForm.getInput();
    const infoPassword: IInputInfo | undefined = this.isMobileLogin ? this.passwordForm.getPasswordInput() : undefined;

    if (info) {
      this.saveBaseUser(info)
        .then((data: ILoginPayload) => {
          if (data.Code === '0000') {
            this.loadBaseUser()
              .then((loadData: ILoginPayload) => {
                if (loadData.Code === '0000') {
                  if (this.isMemberProcess) this.fireSwal(loadData.Succ || false, '更新完成');
                  else this.$router.push('/member/interest_exam');
                }
              });
          }
        });
    }

    if (infoPassword) {
      changeMyPw(infoPassword)
        .then((data: ILoginPayload) => {
          this.fireSwal(data.Succ || false, data.Message || '');
        });
    }
  }

  associate() {
    this.$router.push('/member/member_association');
  }

  fireSwal(succ: boolean, msg: string) {
    Swal.fire({
      toast: true,
      width: 300,
      position: 'top',
      showConfirmButton: false,
      showCloseButton: true,
      background: '#EDFAF5',
      buttonsStyling: false,
      timer: 3000,
      timerProgressBar: true,
      customClass: {
        closeButton: succ ? 'success-toast-close-btn' : 'toast-close-btn',
        popup: succ ? 'success-toast-popup' : 'toast-popup',
        title: succ ? 'success-toast-title' : 'toast-title',
      },
      title: msg,
    });
  }

  get isMemberProcess() {
    return this.$route.query.process === 'member';
  }
}
